import * as React from "react";
import { getAd, getAdInfo } from '../../api/index';

import './advertising.less';
interface ISprops {
    [propsName: string]: any,

}
interface IState {
    [statesName: string]: any
}

export default class MainAdvertising extends React.Component<ISprops, IState>{
    constructor(props: any) {
        super(props);
        this.state = {
            adDetail: {}
        }
    }
    getInitAd(title) {
        getAd({
            label: title
        }).then(res => {
            if (!res.status) {
                this.setState({adDetail:res.data[0] || {}})
            }
        })
    }
    skip = (el) => { 
        getAdInfo({
            id: el.id
        }).then(res => {
            window.location.href = el.link;
        })
    }
    componentDidMount() {
        const { title } = this.props;
        this.getInitAd(title)
    }
    render() {
        const { adDetail } = this.state;
        return (
            <React.Fragment>
                {
                    Object.keys(adDetail).length !== 0 ? 
                        <div className="main-ad" onClick={() => this.skip(adDetail)}>
                            <img src={adDetail.banner} alt={adDetail.name}/>
                        </div>
                        :
                        <div></div>
                }
            </React.Fragment>
        )
    }

}
